<template><section><h1>Icon</h1>
<hr>
<h3>基础用法</h3>
<p>语义化的矢量图形，<code v-pre="" class="code_inline">Bili-UI</code> 使用开源的 <a href="http://www.iconfont.cn/">Iconfont</a> 作为图标库。</p>
<p>通过 <code v-pre="" class="code_inline">value</code> 属性定义不同icon, 通过<code v-pre="" class="code_inline">klass</code>属性来给icon附加<code v-pre="" class="code_inline">class</code>。</p>
<div class="demo-block">
  <p>
    <b-icon value="sort" klass="f20 mr20"></b-icon>  <b-icon value="sort" klass="f26 mr20"></b-icon>  <b-icon value="sort" klass="f32 red mr20"></b-icon>  <b-icon value="sort" klass="f38 yellow mr20"></b-icon>     
  </p>
  <p>
    <b-icon value="top" klass="f38 mr20"></b-icon>   <b-icon value="search" klass="f38 mr20"></b-icon>   <b-icon value="more" klass="f38 mr20"></b-icon>      
  </p> 
</div>
<demo-block>
                        <div slot="desc"><template>
<div class="demo-block">
  <b-icon value="sort" klass="f20 mr20"></b-icon>  <b-icon value="sort" klass="f26 mr20"></b-icon>  <b-icon value="sort" klass="f32 red mr20"></b-icon>  <b-icon value="sort" klass="f38 yellow mr20"></b-icon>        
</div>

<div class="demo-block">
  <b-icon value="top" klass="f38 mr20"></b-icon>   <b-icon value="search" klass="f38 mr20"></b-icon>   <b-icon value="more" klass="f38 mr20"></b-icon>    
</div> 
<template>
</template></template></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-block"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">b-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"sort"</span> <span class="hljs-attr">klass</span>=<span class="hljs-string">"f20 mr20"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">b-icon</span>&gt;</span>  <span class="hljs-tag">&lt;<span class="hljs-name">b-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"sort"</span> <span class="hljs-attr">klass</span>=<span class="hljs-string">"f26 mr20"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">b-icon</span>&gt;</span>  <span class="hljs-tag">&lt;<span class="hljs-name">b-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"sort"</span> <span class="hljs-attr">klass</span>=<span class="hljs-string">"f32 red mr20"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">b-icon</span>&gt;</span>  <span class="hljs-tag">&lt;<span class="hljs-name">b-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"sort"</span> <span class="hljs-attr">klass</span>=<span class="hljs-string">"f38 yellow mr20"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">b-icon</span>&gt;</span>        
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-block"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">b-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"top"</span> <span class="hljs-attr">klass</span>=<span class="hljs-string">"f38 mr20"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">b-icon</span>&gt;</span>   <span class="hljs-tag">&lt;<span class="hljs-name">b-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"search"</span> <span class="hljs-attr">klass</span>=<span class="hljs-string">"f38 mr20"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">b-icon</span>&gt;</span>   <span class="hljs-tag">&lt;<span class="hljs-name">b-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"more"</span> <span class="hljs-attr">klass</span>=<span class="hljs-string">"f38 mr20"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">b-icon</span>&gt;</span>    
