<pre v-pre=""><code v-pre="" class="language-style">&lt;style&gt;
  .mr20{
    margin-right:20px;
  }
  .f20{
    font-size:20px;
  }
  .f26{
    font-size:26px;
  }
  .f32{
    font-size:32px;
  }
  .f38{
    font-size:38px;
  }
  .red{
    color:red
  }
  .yellow{
    color:yellow
  }
&lt;/style&gt; 
