</div></demo-block>
<h6>组件点击事件</h6>
<div class="demo-block">
  <b-icon value="search" klass="f38" @iconClick="iconClick"></b-icon> 
</div>
<demo-block>
                        <div slot="desc"><template>
<b-icon value="search" @iconclick="iconClick"></b-icon> 
<template>
</template></template></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">b-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"search"</span> <span class="hljs-attr">@iconClick</span>=<span class="hljs-string">"iconClick"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">b-icon</span>&gt;</span> 
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
</code></pre>
<pre v-pre=""><code v-pre="" class="language-javascript">&lt;script&gt;
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>:{
      iconClick(){
        alert(<span class="hljs-string">'开始搜索!'</span>)
      }
    }
  }
&lt;<span class="hljs-regexp">/script&gt;
</span></code></pre>
</div></demo-block>
<h3>图标示例</h3>
<template>
  <ul class="icon-wrapper">
    <li class="icons" v-for="icon in icons">
      <b-icon :value="icon"></b-icon>
      <p>{{icon}}</p>
    </li>
  </ul>
</template>
 

</section></template>
<style>
  *{
    padding:0;
    margin:0;
  }
  li{
    list-style:none;
  }
  .mr20{
    margin-right:20px;
  }
  .f20{
    font-size:20px;
  }
  .f26{
    font-size:26px;
  }
  .f32{
    font-size:32px;
  }
  .f38{
    font-size:38px;
  }
  .red{
    color:red
  }
  .yellow{
    color:yellow
  }
  .icon-wrapper{
    display:flex;
    flex-wrap:wrap;
    box-sizing:border-box;
    width:100%;
    border-radius:10px;
    border-top: 1px solid #eee;
    border-left: 1px solid #eee;
  }
  .icon-wrapper li{
    line-height:60px;
    width:calc(100% / 6 - 1px);
    height:120px;
    text-align:center;
    border-right:1px solid #eee;
    border-bottom:1px solid #eee;
    transition: all .3s;
  }
  .icon-wrapper li p{
    margin-top:20px;
    font-weight:bolder;
    line-height:1
  } 
  .icon-wrapper li:hover{
    color:#00a1d6;
  }
</style>
<script>
  export default {
    data(){
      return{
        icons:[
          'home',
          'search',
          'top',
          'sort',
          'more',
          'shopcar-empty',
          'shopcar-fill',
        ]
      }
    },
    methods:{
      iconClick(){
        alert('开始搜索!')
      }
    }
  }
</script>


