<template><section><h1>NavList</h1>
<hr>
<h3>基础用法</h3>
<p>list参数的<code v-pre="" class="code_inline">id</code>对应锚链接元素的id,<code v-pre="" class="code_inline">name</code>为导航的文本,<code v-pre="" class="code_inline">order</code>为导航的排序,为了进行拖动导航对应的页面主体内容需要使用flex布局</p>
<p><b-nav-list v-if="list && list.length" :list="list" :styles="styles"></b-nav-list></p>
<div style="margin-top:100px;display:flex;flex-direction:column">
<div id="a" style="width:100%;height:100vh;background:red;opacity:.3"></div>
<div id="b" style="width:100%;height:100vh;background:blue;opacity:.3"></div>
<div id="c" style="width:100%;height:100vh;background:yellow;opacity:.3"></div>
</div>

<demo-block>
                        <div slot="desc"><template>
<div class="demo-block">
  <b-nav-list :list="list" :styles="styles"></b-nav-list>
  <div style="margin-top:100px;display:flex;flex-direction:column">
    <div id="a" style="width:100%;height:100vh;background:red;opacity:.3"></div>
    <div id="b" style="width:100%;height:100vh;background:blue;opacity:.3"></div>
    <div id="c" style="width:100%;height:100vh;background:yellow;opacity:.3"></div>
  </div>
</div>
<template>
</template></template></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-block"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">b-nav-list</span> <span class="hljs-attr">:list</span>=<span class="hljs-string">"list"</span> <span class="hljs-attr">:styles</span>=<span class="hljs-string">"styles"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">b-nav-list</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"margin-top:100px;display:flex;flex-direction:column"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">id</span>=<span class="hljs-string">"a"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:100%;height:100vh;background:red;opacity:.3"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">id</span>=<span class="hljs-string">"b"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:100%;height:100vh;background:blue;opacity:.3"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">id</span>=<span class="hljs-string">"c"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"width:100%;height:100vh;background:yellow;opacity:.3"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
</code></pre>
<pre v-pre=""><code v-pre="" class="language-javascript">  <span class="hljs-comment">//事例用定时器代替异步请求</span>
&lt;script&gt;
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data(){
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">list</span>: <span class="hljs-literal">null</span>,
        <span class="hljs-attr">styles</span>:{
          <span class="hljs-attr">top</span>:<span class="hljs-string">'200px'</span>,
          <span class="hljs-attr">right</span>:<span class="hljs-string">'200px'</span>,
        }
      }
    },
    mounted(){
      setTimeout(<span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span>{
        <span class="hljs-keyword">this</span>.list = [
          {
              <span class="hljs-attr">id</span>: <span class="hljs-string">'a'</span>,
              <span class="hljs-attr">name</span>: <span class="hljs-string">'red'</span>,
              <span class="hljs-attr">order</span>: <span class="hljs-number">0</span>
            },
            {
              <span class="hljs-attr">order</span>: <span class="hljs-number">1</span>,
              <span class="hljs-attr">id</span>: <span class="hljs-string">'b'</span>,
              <span class="hljs-attr">name</span>: <span class="hljs-string">'blue'</span>
            },
            {
              <span class="hljs-attr">order</span>: <span class="hljs-number">2</span>,
              <span class="hljs-attr">id</span>: <span class="hljs-string">'c'</span>,
              <span class="hljs-attr">name</span>: <span class="hljs-string">'yellow'</span>
            },
        ]
      },<span class="hljs-number">500</span>)
    },
  }
&lt;<span class="hljs-regexp">/script&gt;
</span></code></pre>
</div></demo-block>
<h2>API</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>list</td>
<td>导航列表</td>
<td>Array</td>
<td>{id:String, order:String, name:String}</td>
<td>—</td>
</tr>
<tr>
<td>styles</td>
<td>位置</td>
<td>Object</td>
<td>父级对应样式的style对象</td>
<td>—</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  export default {
    data(){
      return {
        list:null,
        styles:{
          top:'200px',
          right:'200px',
        }
      }
    },
    mounted(){
      setTimeout(()=>{
        this.list = [
          {
              id: 'a',
              name: 'red',
              order: 0
            },
            {
              order: 1,
              id: 'b',
              name: 'blue'
            },
            {
              order: 2,
              id: 'c',
              name: 'yellow'
            },
        ]
      },500)
    },
  }
</script>


